import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import Dasha from '../img/Dasha.png';

import VacantDay from '../img/VacantDay.png';
import './RecordMenuStyle.css';


const osName = platform();

var count=4;

const request = new XMLHttpRequest();
var urlRegUsers = "https://13c098f3a487.ngrok.io/recordPrachka";





const RecordMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="mainMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Laundry</PanelHeader>
		<Div className="MainRecord">
			<Div className= "TextBlockRecord">
				Выберите время, дату и количество машинок
			</Div>
			<Div className= "RecordBlock">
				<Button className="ButtonMothsDay" size="l" mode="outline">
					<div className="FontClass">ПН</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">ВТ</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">СР</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">ЧТ</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">ПТ</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">СБ</div>
				</Button>
				<Button className="ButtonMothsDay" size="l" mode="outline" >
					<div className="FontClass">ВС</div>
				</Button>
			</Div>
			<Div className="RecordBlockDataTime">
				<Button className = "ButtonDataTime" size="l" mode="outline" >
					<Div className = "TimeRecord">14:00</Div>
					<Div className = "CountRecord">{count}</Div>
				</Button>
				<Button className = "ButtonDataTime" size="l" mode="outline">
					<Div className = "TimeRecord">16:00</Div>
					<Div className = "CountRecord">{count}</Div>
				</Button>
				<Button className = "ButtonDataTime" size="l" mode="outline">
					<Div className = "TimeRecord">18:00</Div>
					<Div className = "CountRecord">{count}</Div>
				</Button>
				<Button className = "ButtonDataTime" size="l" mode="outline">
					<Div className = "TimeRecord">20:00</Div>
					<Div className = "CountRecord">{count}</Div>
				</Button>
				<Button className = "ButtonDataTime" size="l" mode="outline">
					<Div className = "TimeRecord">22:00</Div>
					<Div className = "CountRecord">{count}</Div>
				</Button>
			</Div>
			<Div className = "ButtonBlocksRecord">
				<Button className="ButtonBlcokRecord" size="xl" mode="outline">
					<Div className = "FontButtonRecord">ЗАПИСАТЬСЯ</Div>
				</Button>
				<Button className="ButtonBlcokRecord" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					<Div className = "FontButtonRecord">НАЗАД</Div>
				</Button>
			</Div>
		</Div>
	</Panel>
);

RecordMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RecordMenu;
